import React from 'react'
import {Button} from '../ButtonElement'
import { Dropdown } from 'react-bootstrap';
import logo from '../../images/logo.png'
import '@splidejs/splide/dist/css/themes/splide-default.min.css';
// or
import '@splidejs/splide/dist/css/themes/splide-sea-green.min.css';
// or
import '@splidejs/splide/dist/css/themes/splide-skyblue.min.css';
import { Splide, SplideSlide } from '@splidejs/react-splide';
import { 
    Container, 
   
    FormWrap,
    Icon,
    FormContent,
    Form,
    FormH1,
    FormLabel,
    FormInput,
    FormButton,
    Text,
    Text2

} from './TraitsPgElements'
import './trait.css';
import cap from './graphics/baseball-cap.png';
const TraitsPg = () => {
    let primaryOptions = {
        type      : 'loop',
        width     : 500,
        height    : 400,
    	autoplay     : true,
        perPage   : 1,
        perMove   : 1,
        gap       : '1rem',
        pagination: false,
    };
    return (
        <Container>
            {/* here */}
            <Icon to="/">    <img style={{height:"150px", width:"200px"}} src={logo}/> </Icon>

        <div className="">
            <div className="row justify-content-center">
                <div className="offset-2 col-lg-2 col-md-4 col-sm-1 my-5" >

                <Dropdown >
                <Dropdown.Toggle size="lg" variant="success" id="dropdown-basic" style={{padding: "10px 30px"}}>
                   <i> He<span style={{color: "#DA0000"}}>ad</span></i>
                </Dropdown.Toggle>

                <Dropdown.Menu>
                    <Dropdown.Item href="#/action-1" style={{color:"#fff"}}>Head</Dropdown.Item>
                    <Dropdown.Item href="#/action-2"  style={{color:"#fff"}}>Clothing</Dropdown.Item>
                    <Dropdown.Item href="#/action-3"  style={{color:"#fff"}}>Mouth</Dropdown.Item>
                    <Dropdown.Item href="#/action-3"  style={{color:"#fff"}}>Fur/Skin</Dropdown.Item>
                    <Dropdown.Item href="#/action-3"  style={{color:"#fff"}}>Teeth</Dropdown.Item>
                    <Dropdown.Item href="#/action-3"  style={{color:"#fff"}}>Eyewear</Dropdown.Item>
                    <Dropdown.Item href="#/action-3"  style={{color:"#fff"}}>Background</Dropdown.Item>
              
              
                </Dropdown.Menu>
                </Dropdown>


                    <div className="mt-5 pt-3 ps-2">
                        <p>
                        <div className="form-check">
                            <input className="form-check-input" type="checkbox" value="" id="flexCheckDefault"/>
                            <label className="form-check-label text-light" for="flexCheckDefault">
                                All
                            </label>
                        </div>
                        </p>
                        <p>
                        <div className="form-check">
                            <input className="form-check-input" type="checkbox" value="" id="flexCheckDefault"/>
                            <label className="form-check-label text-light" for="flexCheckDefault">
                                Common
                            </label>
                        </div>
                        </p>
                        <p>
                        <div className="form-check">
                            <input className="form-check-input" type="checkbox" value="" id="flexCheckDefault"/>
                            <label className="form-check-label text-light" for="flexCheckDefault">
                                Uncommon
                            </label>
                        </div>
                        </p>
                        <p>
                        <div className="form-check">
                            <input className="form-check-input" type="checkbox" value="" id="flexCheckDefault"/>
                            <label className="form-check-label text-light" for="flexCheckDefault">
                                Rare
                            </label>
                        </div>
                        </p>
                        <p>
                        <div className="form-check">
                            <input className="form-check-input" type="checkbox" value="" id="flexCheckDefault"/>
                            <label className="form-check-label text-light" for="flexCheckDefault">
                                Super Rare
                            </label>
                        </div>
                        </p>
                        <p>
                        <div className="form-check">
                            <input className="form-check-input" type="checkbox" value="" id="flexCheckDefault"/>
                            <label className="form-check-label text-light" for="flexCheckDefault">
                                Mythic
                            </label>
                        </div>
                        </p>
                    </div>

                </div>
                <div className="offset-lg-1 col-lg-4 col-md-6 col-sm-6 justify-content-center">
                    <div className="row  mb-3">
                        <div className="col-lg-10 col-md-10 col-sm-8 my-5" style={{borderRadius: "10px 10px"}} >
                            {/* splide */}
                            <Splide className=""  options={ cap } style={{}}>
                                <SplideSlide >
                                    <img className="img-fluid " src={cap}    alt="Image 1"/>
                                </SplideSlide>
                                <SplideSlide>
                                <img className="img-fluid " src={cap}  alt="Image 1"/>
                                </SplideSlide>
                            </Splide>
                            {/* <img className="img-fluid " src={cap} alt=""/> */}
                        
                        
                        </div>
                        <div className="col-lg-10 col-md-8 col-sm-10 "
                            style={{borderRadius: "10px 10px" , backgroundColor: "#c6a890"}}>
                            <div className="py-1 d-flex">
                                <div>
                                    <p className="p-0 m-0 text-dark fw-bolder text-uppercase">Sun Hut</p>
                                    <p className="p-0 m-0 text-dark fs-8">4.3 have this trait</p>
                                </div>
                                <div className="ms-4">
                                    <p className="p-0 m-0 text-dark fw-bolder fs-7 text-uppercase">Head</p>
                                    <p className="p-0 m-0 text-dark text-uppercase fs-8">Common</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        {/* here */}
         </Container>
    )
}

export default TraitsPg
